import axios from 'axios';
import qs from 'qs';

// const isProduction = process.env.NODE_ENV === 'production';

const defaultConfig = {
  baseURL: process.env.API_URL_NEXT,
  timeout: 15000,
  withCredentials: true
};
axios.interceptors.response.use(response => response, (error) => {
  if (error.response) {
    // message.error(error.response.data.errmsg || error.response.data);
    return error.response;
  } else if (error.request) {
    console.log(error.request);
  } else {
    console.log('Error', error.message);
  }
});
export const getReqNext = async (url, params, options={}) => {
  const dataset = { ...params };
  if (window.self !== window.top && url.indexOf('topic/data') != -1) {
    dataset.preview = 1;
  }
  const ret = await axios({
    ...defaultConfig, url,
    method: 'GET',
    params: dataset,
    ...options
  });
  const { data } = ret;
  return data;
};
export const postReqNext = async (url, data) => {
  const ret = await axios({
    ...defaultConfig, url,
    method: 'POST',
    headers: { 'content-type': 'application/x-www-form-urlencoded' },
    data: qs.stringify(data),
  });
  const { data: dataSet } = ret;
  return dataSet;
};