/**
 * @file 管理 cookie
 *
 * @author soulran(xr@ethercap.com)
 */

export function getCookie(key) {
  return decodeURIComponent(document.cookie.replace(new RegExp('(?:(?:^|.*;)\\s*' + encodeURIComponent(key).replace(/[-.+*]/g, '\\$&') + '\\s*\\=\\s*([^;]*).*$)|^.*$'), '$1')) || null;
}
export function setCookie(key, value, expire, sPath, sDomain, bSecure) {
  if (!key || /^(?:expires|max-age|path|domain|secure)$/i.test(key)) {
    return false;
  }
  var sExpires = '';
  if (expire) {
    switch (expire.constructor) {
      case Number:
        sExpires = expire === Infinity ? '; expires=Fri, 31 Dec 9999 23:59:59 GMT' : '; max-age=' + expire;
        break;
      case String:
        sExpires = '; expires=' + expire;
        break;
      case Date:
        sExpires = '; expires=' + expire.toUTCString();
        break;
    }
  }
  document.cookie = encodeURIComponent(key) + '=' + encodeURIComponent(value) + sExpires + (sDomain ? '; domain=' + sDomain : '') + (sPath ? '; path=' + sPath : '') + (bSecure ? '; secure' : '');
  return true;
}
export function removeCookie(key, sPath, sDomain) {
  if (!key) return false;
  document.cookie = encodeURIComponent(key) + '=; expires=Thu, 01 Jan 1970 00:00:00 GMT' + (sDomain ? '; domain=' + sDomain : '') + (sPath ? '; path=' + sPath : '');
  return true;
}
export default {
  get: getCookie,
  set: setCookie,
  remove: removeCookie
};