import { INVISIBLE, INSIDE, BELOW, ABOVE } from './constants';
/**
 * @param {object} bounds An object with bounds data for the waypoint and
 *   scrollable parent
 * @return {string} The current position of the waypoint in relation to the
 *   visible portion of the scrollable parent. One of the constants `ABOVE`,
 *   `BELOW`, `INSIDE` or `INVISIBLE`.
 */

export default function getCurrentPosition(bounds) {
  if (bounds.boundaryBottom - bounds.boundaryTop === 0) {
    return INVISIBLE;
  } // top is within the boundary


  if (bounds.boundaryTop <= bounds.waypointTop && bounds.waypointTop <= bounds.boundaryBottom) {
    return INSIDE;
  } // bottom is within the boundary


  if (bounds.boundaryTop <= bounds.waypointBottom && bounds.waypointBottom <= bounds.boundaryBottom) {
    return INSIDE;
  } // top is above the boundary and bottom is below the boundary


  if (bounds.waypointTop <= bounds.boundaryTop && bounds.boundaryBottom <= bounds.waypointBottom) {
    return INSIDE;
  }

  if (bounds.boundaryBottom < bounds.waypointTop) {
    return BELOW;
  }

  if (bounds.waypointTop < bounds.boundaryTop) {
    return ABOVE;
  }

  return INVISIBLE;
}