import { postReqNext, getReqNext } from './fetch';

// 获取内容包数据
export const getListData = async (params) => {
    // https://m.mafengwo.cn/nb/webapi/qianxun/aggregate/get_detail?page_id=other_content-111000001214358117
    const { data, errno, message } = await getReqNext('https://m.mafengwo.cn/nb/webapi/qianxun/aggregate/get_recommend_list', {
        page_id: 'other_content-1110000012357142201',
        num: 20,
        ...params,
    });
    return { data, errno, message };
};

// 通用抽奖接口
export const like = async (params) => {
    // https://m.mafengwo.cn/wengs/detail.php/like
    const { errno, data, errmsg } = await postReqNext('https://m.mafengwo.cn/wengs/detail.php/like', {
        ...params,
    });
    return { data, errno, errmsg };
};
