<template>
  <div class="waterfall-section">
  <div>版本0.2.0</div>
    <ul class="nav-section">
      <li
        @click="() => clickNavItem(0)"
        class="nav-item"
        :class="{ active: currentTabIndex === 0 }"
      >
        推荐
      </li>
      <li
        @click="() => clickNavItem(1)"
        class="nav-item"
        :class="{ active: currentTabIndex === 1 }"
      >
        最新
      </li>
    </ul>
    <div class="scroll-section">
      <ul class="list-content">
        <li class="list-content-item" v-for="item in list" :key="item.data.id">
          <div
            class="list-content-item__main"
            @click="() => jumpDetail(item.data.jump_url)"
          >
            <img :src="item.data.img.bimg" alt="" />
            <div class="location-section">
              <img
                class="location-icon"
                src="../images/location-icon.png"
                alt=""
              />
              <span class="location-name">
                {{ item.data.mdd.name }}
              </span>
            </div>
          </div>
          <div class="list-content-item__desc">
            <span>
              {{ item.data.content }}
            </span>
          </div>
          <div class="list-content-item__userInfo">
            <div class="mfw-waterfall-user">
              <img class="mfw-waterfall-icon" :src="item.data.owner.logo" />
              <div
                class="mfw-waterfall-name textOverflow_1"
              >
                {{ item.data.owner.name }}
              </div>
            </div>
            <div class="mfw-waterfall-like" @click="() => clickLike(item.data)">
              <img
                v-if="!!item.data.is_liked"
                class="mfw-waterfall-like-icon"
                src="data:image/svg+xml;base64,"
                alt=""
              />
              <img
                v-else
                class="mfw-waterfall-like-icon"
                src="data:image/svg+xml;base64,"
                alt=""
              />
              <span class="mfw-waterfall-like-num">{{
                item.data.num_like
              }}</span>
            </div>
          </div>
        </li>
      </ul>
      <m-paging
        doneNote="已经到底啦 ~"
        :isDone="!hasNext"
        :onLoad="loadNextPage"
      ></m-paging>
    </div>
  </div>
</template>

<script>
import { Paging } from "@mfw/mfw-ui/es/components/paging";
import { jumpNewPage } from "@mfw/xjs/services/mfwapp";
import { getListData, like } from '../service/index';
export default {
  components: {
    MPaging: Paging,
  },
  props: {
    contentTabId: {
      type: String,
      default: "b18fe2db3d088d3f",
    },
  },
  data() {
    return {
      currentTabIndex: 0,
      list: [],
      hasNext: true,
      nextBoundary: 0, // currentPage
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    async clickLike(item) {
      const params = {
        cancel: item.is_liked ? 1 : 0,
        id: item.id,
      };
      try {
        await like(params);
        this.$set(item, "is_liked", 1 - item.is_liked);
      } catch (error) {
        console.log(error)
      }
    },
    clickNavItem(index) {
      if (this.currentTabIndex === index) return;
      this.currentTabIndex = index;
      this.hasNext = true;
      this.nextBoundary = 0;
      this.getData();
    },
    async getData() {
      const tabIds = ["", this.contentTabId];
      const params = {
        tab_id: tabIds[this.currentTabIndex],
        boundary: this.nextBoundary,
      };
      const { data } = await getListData(params);
      const contents = data.list || [];
      if (!params.boundary) {
        this.list = contents;
      } else {
        this.list.push(...contents);
      }

      this.nextBoundary = data.page.next_boundary;
      if (!data.page.next) {
        this.hasNext = false;
      }
    },

    /**
     * 上拉获取
     */
    async loadNextPage() {
      return this.getData();
    },

    jumpDetail(url) {
      jumpNewPage(url, "blank");
    },
  },
};
</script>

